<template>
  <div class="corporate">
    <van-nav-bar title="企业账号" left-text="返回" left-arrow :fixed="true" @click-left="back"></van-nav-bar>
    <div class="corporate-bg"></div>
    <div class="cor-context">
      <div class="cor-box">
        <div class="btn">
          <van-button type="primary" size="large" @click="apply">立即申请&nbsp;&nbsp;></van-button>
        </div>
        <div class="box-contxt">
          <div class="box-item clearfix">
            <div class="box-left fl">
              <div class="txt">商务</div>
              <div>
                <img src="../assets/images/sw.png" alt />
              </div>
            </div>
            <div class="box-right fl">
              <div>企业开通账户，预存金额，给员工开通消费权限，员工可使用企业账户余额同一订咖啡，更好的满足商务洽谈、工作接待、会议咖啡等多场景需要，让职场更专业。</div>
            </div>
          </div>
          <div class="box-item clearfix">
            <div class="box-left fl">
              <div class="txt">优惠</div>
              <div>
                <img src="../assets/images/yh.png" alt />
              </div>
            </div>
            <div class="box-right fl">
              <div>预存企业账户，享受专属优惠：全场饮品7.5折，轻食9.5折（若遇市场统一促销活动，则享受最佳优惠）。</div>
            </div>
          </div>
          <div class="box-item clearfix">
            <div class="box-left fl">
              <div class="txt">高效</div>
              <div>
                <img src="../assets/images/gx.png" alt />
              </div>
            </div>
            <div class="box-right fl">
              <div>App在线点单，免排队，自提外卖，30分钟新鲜速达，省时省力。</div>
            </div>
          </div>
        </div>
        <div class="flow">
          <div class="flow-title">申请流程</div>
          <div class="flow-context">
            <div class="flow-item clearfix">
              <div class="flow-left fl">
                <img src="../assets/images/qy1.gif" alt />
              </div>
              <div class="flow-right fl">企业在线填写申请信息</div>
            </div>
            <div class="flow-item clearfix">
              <div class="flow-left fl">
                <img src="../assets/images/qy2.gif" alt />
              </div>
              <div class="flow-right fl">申请通过后24小时内开通企业账户</div>
            </div>
            <div class="flow-item clearfix">
              <div class="flow-left fl">
                <img src="../assets/images/qy3.png" alt />
              </div>
              <div class="flow-right fl">企业在线预存金额并开通管理权限</div>
            </div>
            <div class="flow-item clearfix">
              <div class="flow-left fl">
                <img src="../assets/images/qy4.gif" alt />
              </div>
              <div class="flow-right fl">企业配置员工消费权限</div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-popup round v-model="show" closeable>
      <div class="nrong">
        <div class="sqing">在线申请</div>
        <van-form>
          <van-field
          v-model="applications.firm" 
          label="企业名称" placeholder="请与营业执照保持一致（必填）" />
          <van-field
          v-model="applications.name"
           label="联系人" placeholder="填写联系人真实姓名（必填）" />
          <van-field
          v-model="applications.phone"
           label="联系电话" placeholder="填写联系人手机号码（必填）" />
        </van-form>
        <div class="sq-btn">
          <van-button block color="#0C34BA" native-type="submit" @click="dianji">提交申请</van-button>
        </div>
        <div class="bottom-txt">点击提交按钮，即表示已阅读并同意《企业服务条款》</div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/corporate.less";
import {validForm} from '../assets/js/validForm'
export default {
  name: "Corporate",
  data() {
    return {
      show: false,
      applications:{
          firm:'',
          name:'',
          phone:''

      }
    };
  },
  created() {},
  methods: {
    back() {
      this.$router.go(-1);
    },

    apply() {
      this.show = true;
    },
    dianji(){
        let user = this.applications;
        
        let o = {
        firm: {
          value: user.firm,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: '昵称格式不正确'
        },
        name: {
          value: user.name,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: '昵称格式不正确'
        },
        phone: {
          value: user.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: '手机号格式不正确'
        }
      };
      if(!validForm.valid(o)){
      return;
      }
      this.show = false;
    // 成功后清空数据
      this.$toast.success('申请成功');
      if(this.show == false){
          for (let key in user) {
              user[key] = '';
            }
      }
      
    }
  }
};
</script>

<style lang="less" scoped>
</style>